* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: Inter, sans-serif;
}

main{
    width: 100%;
    min-height: 100vh;
}
.active{
    color: #000000 !important;
    font-weight: bold!important;
    cursor: default!important;
    font-size: 18px!important;
}

#root {
    display:flex;
}

.project-preview {
    display: flex;
    align-items: center;
    padding: 20px;
    margin: 40px;
    box-shadow: 0 1px 1px rgb(20 35 52 / 3%), 0 1px 3px rgb(20 35 52 / 4%), 0 2px 7px rgb(20 35 52 / 5%), 0 6px 24px rgb(20 35 52 / 7%);
    border-radius: 20px;
}

.project-preview__description {
    padding-right: 160px;
}

.project-preview__photo-count {
    padding-right: 40px;
}

.project-preview__recent-users {
    padding-left: 40px;
    padding-right: 160px;
}

.project-preview__name {
    font-size: 36px;
    line-height: 30px;
    color: #2B283A;
}

.project-preview__location {
    font-weight: 400;
    font-size: 30px;
    line-height: 12px;
    color: #2B283A;
}

.project-preview__last-update-time {
    font-size: 16px;
    line-height: 150%;
    color: #2B283A;
}

.project-preview__title {
    font-weight: normal;
    font-size: 16px;
    line-height: 150%;
    color: #2B283A;
}

.project-preview__photos > p {
    font-style: italic;
    font-size: 16px;
    line-height: 150%;
    color: #2B283A;
}

/*header*/
.header {
    display: flex;
    margin: 100px 40px;
    align-items: baseline;
}

.header__location-name {
    margin: 0;
    font-size: 44px;
    line-height: 30px;
    color: #2B283A;
}

.header__input {
    margin-left: 40px;
    margin-right: auto;
    border-radius: 30px;
    border: 1px solid #cccccc;
    font-size: 24px;
    padding: 10px;
}

.header__button {
    border-radius: 10px;
    font-size: 18px;
    font-weight: bolder;
    padding: 20px;
    color: white;
    border: 0;
    background-color: #154d3b;
}

/*sidebar*/

.nav {
    max-width: 270px;
    background: #154d3b;
    color: #fff;
    padding: 20px;
}

.nav__title {
    font-size: 28px;
    font-weight: 700;
    margin-bottom: 20px;
}

.nav__item {
    display: flex;
    align-items: center;
    font-size: 16px;
    position: relative;
    border-bottom: 1px solid #fff;
    cursor: pointer;
    padding: 10px 0;
    transition: all .2s ease-in-out;
    text-decoration: none;
    color: white;
}
.nav__item:hover{
    font-size: 18px;
    color: #2B283A;
}

.nav__item__icon {
    width: 20px;
    margin: 10px 20px 10px 0;
}

